/* eslint-disable react/prop-types */
/* eslint-disable no-undef */
import React, { useState, useEffect } from 'react';
import Swal from 'sweetalert2';
import './styles/BadgeDetails.css';
import PageLoading from '../components/PageLoading';
import BadgeDetailsContainer from '../components/BadgeDetailsContainer';
import BadgeDetailsHero from '../components/BadgeDetailsHero';
import api from '../api';

function BadgeDetails(props) {
  const [state, setState] = useState({
    badge: {},
    loading: true,
    error: null,
  });

  useEffect(() => {
    async function fetchData() {
      try {
        const response = await api.badges.read(props.match.params.id);
        setState({
          badge: response,
          loading: false,
        });
      } catch (error) {
        if (error.response.status === 404) {
          Swal.fire({
            title: 'The selected badge was not found',
            type: 'error',
            confirmButtonText: 'Go back',
            allowOutsideClick: false,
            allowEnterKey: false,
            allowEscapeKey: false,
          }).then(() => {
            props.history.push('/badges');
          });
        }
      }
    }

    fetchData();
  }, []);

  const handleDeleteBadge = async () => {
    try {
      Swal.fire({
        title: 'Deleting badge',
        type: 'info',
        showConfirmButton: false,
        allowOutsideClick: false,
        allowEnterKey: false,
        allowEscapeKey: false,
      });

      await api.badges.remove(props.match.params.id);

      Swal.fire({
        title: 'Badge deleted',
        type: 'success',
        allowOutsideClick: false,
        allowEnterKey: false,
        allowEscapeKey: false,
      }).then(() => {
        props.history.push('/badges');
      });
    } catch (error) {
      //
    }
  };


  if (state.loading) {
    return (
      <div className="h-100">
        <PageLoading />
      </div>
    );
  }

  return (
    <div>
      <BadgeDetailsHero badge={state.badge} />

      <BadgeDetailsContainer badge={state.badge} handleDeleteBadge={handleDeleteBadge} />
    </div>
  );
}

export default BadgeDetails;
