/* eslint-disable react/prop-types */
import React from 'react';
import { Link } from 'react-router-dom';

const BadgeForm = ({
  onChange, formValues, onSubmit, action,
}) => (
  <div>
    <h1>
      <span>{ action }</span>
      attendant
    </h1>

    <form onSubmit={onSubmit}>
      <div className="form-group">
        <label htmlFor="FirstName">First Name</label>
        <input onChange={onChange} type="text" className="form-control" value={formValues.form.firstName} name="firstName" />
      </div>
      <div className="form-group">
        <label htmlFor="LastName">Last Name</label>
        <input onChange={onChange} type="text" className="form-control" value={formValues.form.lastName} name="lastName" />
      </div>
      <div className="form-group">
        <label htmlFor="Email">Email</label>
        <input onChange={onChange} type="email" className="form-control" value={formValues.form.email} name="email" />
      </div>
      <div className="form-group">
        <label htmlFor="JobTitle">Job Title</label>
        <input onChange={onChange} type="text" className="form-control" value={formValues.form.jobTitle} name="jobTitle" />
      </div>
      <div className="form-group">
        <label htmlFor="Twitter">Twitter</label>
        <input onChange={onChange} type="text" className="form-control" value={formValues.form.twitter} name="twitter" />
      </div>

      <div className="float-right">
        <Link className="btn btn-secondary mr-2" to="/badges">
            Cancel
        </Link>
        <button type="submit" className="btn btn-primary">
            Save
        </button>
      </div>
    </form>
  </div>
);

export default BadgeForm;
