import { useState, useMemo } from 'react';

export default function useSearchBadges(Badges) {
  const [query, setQuery] = useState('');
  const [filteredBadges, setFilteredBadges] = useState(Badges);
  function filterBadges() {
    const results = Badges.filter((Badge) => `${Badge.firstName} ${Badge.lastName}`.toLowerCase().includes(query.toLowerCase()));
    setFilteredBadges(results);
  }

  useMemo(filterBadges, [Badges, query]);

  return {
    query,
    filteredBadges,
    setQuery,
  };
}
