/* eslint-disable react/prop-types */
import React, { useState } from 'react';
import Swal from 'sweetalert2';
import './styles/BadgeNew.css';
import Badge from '../components/Badge';
import BadgeForm from '../components/BadgeForm';
import Hero from '../components/Hero';
import api from '../api';
import PageError from '../components/PageError';

const BadgeNew = (props) => {
  const [data, setData] = useState({
    form: {
      firstName: '',
      lastName: '',
      jobTitle: '',
      email: '',
      twitter: '',
    },
    loading: false,
    error: null,
  });

  const handleChange = (e) => {
    setData({
      form: {
        ...data.form,
        [e.target.name]: e.target.value,
      },
      loading: data.loading,
      error: data.error,
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    setData({
      form: data.form,
      loading: true,
      error: null,
    });

    try {
      Swal.fire({
        title: 'Adding new badge',
        type: 'info',
        showConfirmButton: false,
        allowOutsideClick: false,
        allowEnterKey: false,
        allowEscapeKey: false,
      });

      await api.badges.create(data.form);

      setData({
        form: data.form,
        loading: false,
        error: null,
      });

      Swal.fire({
        title: 'New badge added',
        type: 'success',
        allowOutsideClick: false,
        allowEnterKey: false,
        allowEscapeKey: false,
      }).then(() => {
        props.history.push('/badges');
      });
    } catch (error) {
      setData({
        form: data.form,
        loading: false,
      });
    }
  };

  if (data.error) {
    return <PageError error={data.error} />;
  }

  return (
    <>
      <Hero />

      <div className="container">
        <div className="row">
          <div className="col-12 col-lg-6">
            <Badge
              firstName={data.form.firstName || 'FIRST_NAME'}
              lastName={data.form.lastName || 'LAST_NAME'}
              jobtitle={data.form.jobTitle || 'JOB_TITLE'}
              twitter={data.form.twitter || 'TWITTER'}
              email={data.form.email || 'EMAIL'}
            />
          </div>
          <div className="col-12 col-lg-6 mb-3">
            <BadgeForm
              onChange={handleChange}
              onSubmit={handleSubmit}
              formValues={data}
              action="New "
            />
          </div>
        </div>
      </div>
    </>
  );
};

export default BadgeNew;
