/* eslint-disable react/no-unescaped-entities */
/* eslint-disable jsx-a11y/label-has-associated-control */
/* eslint-disable react/jsx-one-expression-per-line */
/* eslint-disable react/prop-types */
import React from 'react';
import { Link } from 'react-router-dom';
import useSearchBadges from '../hooks/useSearchBadges';
import './styles/BadgesList.css';
import Gravatar from './Gravatar';

const BadgesList = ({ Badges }) => {
  const { query, filteredBadges, setQuery } = useSearchBadges(Badges);

  function handleFilterOnChange(e) {
    setQuery(e.target.value);
  }

  return (
    <div className="BadgesList">
      <div className="form-group">
        <label>
          Filter Badges
        </label>
        <input
          type="text"
          value={query}
          className="form-control"
          onChange={handleFilterOnChange}
        />
      </div>
      <ul className="list-unstyled row">
        {
          filteredBadges.map((badge) => (
            <li className="col-12 card mb-3" key={badge.id}>
              <Link className="text-reset text-decoration-none" to={`/badges/${badge.id}`}>
                <div className="float-left mr-3">
                  <Gravatar className="img-fluid rounded-circle" email={badge.email} />
                </div>
                <h5 className="BadgeList__name">
                  {badge.firstName}
                  {badge.lastName}
                </h5>

                <a href={`https://www.twitter.com/${badge.twitter}`} target="_blank" rel="noopener noreferrer">
                  <i className="fab fa-twitter mr-1" />
                  @{badge.twitter}
                </a>

                <p className="mb-0">
                  {badge.jobTitle}
                </p>
              </Link>
            </li>
          ))
        }
      </ul>
      {filteredBadges.length === 0 && (
        <h1>No badges not found</h1>
      )}
    </div>
  );
};

export default BadgesList;
