import axios from 'axios';

// const BASE_URL = 'http://localhost:3001';

// const delay = (ms) => new Promise((resolve) => setTimeout(resolve, ms));
// const randomNumber = (min = 0, max = 1) => Math.floor(Math.random() * (max - min + 1)) + min;
// const simulateNetworkLatency = (min = 30, max = 1500) => delay(randomNumber(min, max));

async function callApi(endpoint, method = 'get', body = {}, options = {}) {
  // await simulateNetworkLatency();

  options.headers = {
    'Content-Type': 'application/json',
    Accept: 'application/json',
  };

  const url = endpoint;

  const { data } = await axios({
    url, method, data: body, options,
  });

  return data;
}

const api = {
  badges: {
    list() {
      return callApi('/badges');
    },
    create(badge) {
      return callApi('/badges', 'post', badge);
    },
    read(badgeId) {
      return callApi(`/badges/${badgeId}`);
    },
    update(badgeId, updates) {
      return callApi(`/badges/${badgeId}`, 'PUT', updates);
    },
    remove(badgeId) {
      return callApi(`/badges/${badgeId}`, 'DELETE');
    },
  },
};

export default api;
