/* eslint-disable react/prop-types */
import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import Badge from './Badge';
import DeleteBadgeModal from './DeleteBadgeModal';

export default function BadgeDetailsContainer({ badge, handleDeleteBadge }) {
  const [state, setState] = useState({
    isOpen: false,
  });

  const handleModalState = () => {
    setState({
      isOpen: !state.isOpen,
    });
  };

  return (
    <div className="container">
      <div className="row align-items-center">
        <div className="col-12 col-md-6">
          <Badge
            firstName={badge.firstName || 'FIRST_NAME'}
            lastName={badge.lastName || 'LAST_NAME'}
            jobtitle={badge.jobTitle || 'JOB_TITLE'}
            twitter={badge.twitter || 'TWITTER'}
            email={badge.email || 'EMAIL'}
          />
        </div>
        <div className="BadgeDetails__actions col-12 col-md-6">
          <h2>Actions</h2>
          <div className="BadgeDetails__buttons">
            <Link className="btn-secondary btn" to="/badges">
              Go back
            </Link>
          </div>
          <div className="BadgeDetails__buttons">
            <Link className="btn-primary btn" to={`/badges/${badge.id}/edit`}>
              Edit
            </Link>
          </div>
          <div className="BadgeDetails__buttons">
            <button onClick={handleModalState} type="button" className="btn btn-danger">
              Delete
            </button>
            <DeleteBadgeModal
              handleModalState={handleModalState}
              isOpen={state.isOpen}
              handleDeleteBadge={handleDeleteBadge}
            />
          </div>
        </div>
      </div>
    </div>
  );
}
